'use client';

import React, { useState, useEffect } from 'react';

const TerminalHero = () => {
    const [text, setText] = useState('');
    const [isDeleting, setIsDeleting] = useState(false);
    const [loopNum, setLoopNum] = useState(0);
    const [typingSpeed, setTypingSpeed] = useState(150);

    const words = ["Students", "Teachers", "Builders", "Everyone"];

    useEffect(() => {
        const handleTyping = () => {
            const i = loopNum % words.length;
            const fullText = `AI For ${words[i]}`;

            setText(isDeleting
                ? fullText.substring(0, text.length - 1)
                : fullText.substring(0, text.length + 1)
            );

            // Typing Speed Logic
            setTypingSpeed(isDeleting ? 30 : 150);

            // If word is complete
            if (!isDeleting && text === fullText) {
                // If it's the LAST word ("Everyone"), stop deleting.
                if (words[i] === "Everyone") {
                    return;
                }
                setTimeout(() => setIsDeleting(true), 2000); // Pause before deleting
            }
            // If deleting is complete
            else if (isDeleting && text === '') {
                setIsDeleting(false);
                setLoopNum(loopNum + 1);
            }
        };

        const timer = setTimeout(handleTyping, typingSpeed);
        return () => clearTimeout(timer);
    }, [text, isDeleting, loopNum, typingSpeed, words]);

    return (
        <div className="w-full max-w-lg mx-auto">
            {/* Terminal Window Container */}
            <div className="relative bg-gray-900/90 backdrop-blur-sm rounded-xl border border-gray-700 shadow-2xl overflow-hidden font-mono text-sm sm:text-base leading-normal">

                {/* Top Bar (Mac Style) */}
                <div className="bg-gray-800/50 px-4 py-2 flex items-center gap-2 border-b border-gray-700">
                    <div className="w-3 h-3 rounded-full bg-red-500 hover:bg-red-600 transition-colors"></div>
                    <div className="w-3 h-3 rounded-full bg-yellow-500 hover:bg-yellow-600 transition-colors"></div>
                    <div className="w-3 h-3 rounded-full bg-green-500 hover:bg-green-600 transition-colors"></div>
                    <div className="ml-2 text-xs text-gray-400 select-none">tinkerhub — -zsh</div>
                </div>

                {/* Terminal Body */}
                <div className="p-6 h-48 sm:h-56 flex flex-col justify-start text-gray-300">
                    <div className="mb-2 text-green-400">
                        ➜  ~ <span className="text-blue-400">init</span> tinkerhub-campaign
                    </div>
                    <div className="mb-4 text-gray-400">
                        Loading modules... <br />
                        <span className="text-yellow-500">⚠ v2.0 detected</span>
                    </div>

                    {/* Typing Effect Line */}
                    <div className="flex items-center">
                        <span className="text-purple-400 mr-2">➜</span>
                        <span className="text-white font-bold text-lg sm:text-xl">
                            {text}
                        </span>
                        <span className="w-[3px] h-6 bg-white ml-1 animate-pulse"></span>
                    </div>

                    {/* Optional Success Message shown only when "Everyone" is reached */}
                    {text === "AI For Everyone" && (
                        <div className="mt-4 text-green-400 animate-fade-in">
                            ✓ Access granted to all citizens.
                        </div>
                    )}
                </div>
            </div>

            {/* Decorative Glow behind the terminal */}
            <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[90%] h-[90%] bg-purple-500/20 blur-[100px] -z-10 rounded-full"></div>
        </div>
    );
};

export default TerminalHero;
